import {Component, OnInit, ViewChild, ElementRef} from '@angular/core';
import {AuthService} from '../services/auth.service';
import {Router} from '@angular/router';
import {Clipboard} from '@ionic-native/clipboard/ngx';
import {Promotion} from '../models/Promotion';

@Component({
    selector: 'app-tab-list-promos',
    templateUrl: './tab-list-promos.page.html',
    styleUrls: ['./tab-list-promos.page.scss'],
})
export class TabListPromosPage implements OnInit {
    promos: Array<Promotion> = [
        {
            code: 'SUPER50',
            dateExpiration: new Date(2020, 3, 9),
            description: '30% de réduction sur Sneakers Adidas'
        },
        {
            code: 'KADO20',
            dateExpiration: new Date(2020, 3, 7),
            description: '20% de réduction sur Jean Levi\'s'
        },
        {
            code: 'zbu',
            dateExpiration: new Date(2020, 4, 1),
            description: 'blablalbal'
        },
        {
            code: 'zbu',
            dateExpiration: new Date(2020, 4, 1),
            description: 'blablalbal'
        },
        {
            code: 'zbu',
            dateExpiration: new Date(2020, 4, 1),
            description: 'blablalbal'
        }
    ];
    CopyTextAreaText = 'Sample text to copy!';
    PasteTextAreaText = 'Paste here!';

    constructor(private authService: AuthService, private router: Router, private clipboard: Clipboard) {
    }

    ngOnInit() {
    }

    logout() {
        this.authService.doLogout();
        this.router.navigate(['login']);
    }


    // Copy Event
    copyText() {
        this.clipboard.copy(this.CopyTextAreaText);
    }

    // Paste Event
    pasteText() {
        this.clipboard.paste().then(
            (resolve: string) => {
                this.PasteTextAreaText = resolve;
                console.log(resolve);
            },
            (reject: string) => {
                console.error('Error: ' + reject);
            }
        );
    }

    // Clear Event
    clearClipboard() {
        this.clipboard.clear();
    }
}
